<template>
	<v-container class="pb-10">

		<v-row>
			<v-col>
				<v-dialog v-model="addDialog.show" width="500">
					<template v-slot:activator="{ on }">
						<v-btn rounded class="float-right" color="green lighten-2 white--text" dark v-on="on" > Agregar </v-btn>
					</template>
					<v-card>
						<v-form @submit.prevent="addItem">
							<v-card-title primary-title class="green lighten-2 white--text"> Agregar producto </v-card-title>
							<v-container>
								<v-text-field outlined v-model="addDialog.name" label="Nombre" autofocus/>
								<v-text-field outlined v-model="addDialog.price" label="Precio" type="number"/>
								<v-text-field outlined v-model="addDialog.quantity" label="Cantidad" type="number"/>
							</v-container>
							<v-card-actions>
								<v-spacer></v-spacer>
								<v-btn class="green lighten-2 white--text" @click="addItem"> Agregar </v-btn>
							</v-card-actions>
						</v-form>
					</v-card>
				</v-dialog>
			</v-col>
		</v-row>

		<v-row>
			<v-col>
				<v-simple-table>
					<template v-slot:default>
						<thead>
							<tr>
								<th class="text-left"> Producto </th>
								<th class="text-left"> Precio </th>
								<th class="text-left"> Cantidad </th>
								<th class="text-left"> Subtotal </th>
							</tr>
						</thead>
						<tbody>
							<tr v-for="(item, index) in items" :key="index" class="relative"
								@touchstart.prevent="listItemTouchStart"
								@touchmove.prevent="slideListItem"
								@touchend.prevent="listItemTouchEnd($event, item.id)"
							>
								<td
									@touchend.prevent="listItemTouchEnd(item, 'name', 'text')"
									@click="showEditDialog(item, 'name', 'text')"
								>
									{{item.name}}
								</td>
								<td
									@touchend.prevent="listItemTouchEnd(item, 'price', 'number')"
									@click="showEditDialog(item, 'price', 'number')"
								>
									{{format(item.price)}}
								</td>
								<td
									@touchend.prevent="listItemTouchEnd(item, 'quantity', 'number')"
									@click="showEditDialog(item, 'quantity', 'number')"
								>
									{{item.quantity}}
								</td>
								<td> {{format(item.subtotal)}} </td>
							</tr>
						</tbody>
					</template>
				</v-simple-table>
			</v-col>
		</v-row>

		<v-dialog v-model="editDialog.show" width="500">
			<v-card>
				<v-form @submit.prevent="editItemValue">
					<v-card-title primary-title class="green lighten-2 white--text"> {{editDialog.title}} </v-card-title>
					<v-container>
						<v-text-field :type="editDialog.inputType" v-model="editDialog.input" @focus="selectText" autofocus solo required />
					</v-container>
					<v-card-actions>
						<v-spacer></v-spacer>
						<v-btn class="green lighten-2 white--text" @click="editItemValue"> Cambiar </v-btn>
					</v-card-actions>
				</v-form>
			</v-card>
		</v-dialog>

		<v-footer fixed padless height="50px">
			<v-card flat padless tile width="100%" class="green lighten-2 text-center" >
				<v-card-text class="white--text">
					<strong>Total: {{format(total)}}</strong>
				</v-card-text>
			</v-card>
		</v-footer>
	</v-container>
</template>

<script>
	export default {
		name: 'App',
		data() {
			return {
				items: [],
				editDialog: {
					show: false,
					title: null,
					inputType: null,
					input: null,
					itemReference: null,
					itemProperty: null
				},
				addDialog: {
					show: false,
					name: null,
					price: null,
					quantity: null,
					subtotal: 0
				},
				itemRemoval: {
					parentOffset: null,
					contactPointX: null,
					itemWidth: null,
					slipped: null
				}
			}
		},
		methods: {
			listItemTouchStart(event) {
				const offsets = event.target.parentElement.getBoundingClientRect()
				this.itemRemoval.itemLeftBound = offsets.left
				this.itemRemoval.contactPointX = event.targetTouches[0].pageX
				this.itemRemoval.removalOffset = (offsets.right - offsets.left) * 0.4
			},
			slideListItem(event) {
				const objStyle = event.target.parentElement.style
				const className = event.target.parentElement.className
				const touch = event.targetTouches[0]
				const newPositionX = touch.pageX - this.itemRemoval.contactPointX
				this.itemRemoval.slipped = true
				// Positioning
				if ( newPositionX>0 ) {
					objStyle.left = newPositionX + 'px'
				} else {
					objStyle.left = '0px'
					if (touch.pageX > this.itemRemoval.itemLeftBound) {
						this.itemRemoval.contactPointX = touch.pageX
					}
				}
				// Removal appearance handling
				if (newPositionX > this.itemRemoval.removalOffset) {
					if ( !className.split(' ').includes('remove') ) {
						event.target.parentElement.className += ' remove'
					}
				} else {
					event.target.parentElement.className = className.split(' ').filter(singleClass => singleClass!='remove').join(' ')
				}
			},
			listItemTouchEnd(event, itemId) {
				if (this.itemRemoval.slipped) {
					const className = event.target.parentElement.className
					const objStyle = event.target.parentElement.style
					const leftOffset = objStyle.left.replace('px', '')
					if (leftOffset>this.itemRemoval.removalOffset) {
						this.items = this.items.filter(item => item.id!=itemId)
					}
					objStyle.left = '0px'
					event.target.parentElement.className = className.split(' ').filter(singleClass => singleClass!='remove').join(' ')
				} else {
					this.showEditDialog(...arguments)
				}
				this.itemRemoval.slipped = null
			},
			showEditDialog(item, property, inputType) {
				const itemName = item.name.toLowerCase()
				const titles = {
					name: `Cambiar nombre de ${itemName}`,
					price: `Precio de ${itemName}`,
					quantity: `Cantidad de ${itemName}`
				}
				this.editDialog.title = titles[property]
				this.editDialog.inputType = inputType
				this.editDialog.input = item[property]
				this.editDialog.show = true
				this.editDialog.itemReference = item
				this.editDialog.itemProperty = property
			},
			selectText(event) {
				event.target.select()
			},
			editItemValue() {
				const editDialog = this.editDialog
				const item  = this.editDialog.itemReference
				item[editDialog.itemProperty] = editDialog.input
				if (editDialog.itemProperty==='quantity' || editDialog.itemProperty==='price') {
					item.subtotal = item.quantity * item.price
				}
				editDialog.show = false
				editDialog.title = ''
				editDialog.input = ''
				editDialog.itemReference = null
				editDialog.itemProperty = null
			},
			format(number) {
				let segment
				const decimalTest = String(number).match(/^[0-9]+\.([0-9]+)$/)
				let formatted = decimalTest? `,${decimalTest[1]}` : ''
				number = Math.floor(number)
				while(number>999) {
					segment = String(number%1000)
					if (segment.length<2) {
						segment = `00${segment}`
					} else if (segment.length<3) {
						segment = `0${segment}`
					}
					formatted = `.${segment}${formatted}`
					number = Math.floor(number/1000)
				}
				return number+formatted
			},
			addItem() {
				this.items.push({
					id: Date.now(),
					name: this.addDialog.name,
					price: this.addDialog.price,
					quantity: this.addDialog.quantity,
					subtotal: this.addDialog.quantity*this.addDialog.price
				})
				this.addDialog.name = this.addDialog.price = this.addDialog.quantity = this.addDialog.subtotal = null
				this.addDialog.show = false
			}
		},
		computed: {
			total() {
				let total = 0
				for (let item of this.items) {
					total += item.subtotal
				}
				return total
			}
		}
	}
</script>

<style scoped>
	.remove {
		opacity: 0.25
	}
	.relative {
		position: relative
	}
</style>